<template>
	<view class="address">
		<view class="addre" v-if="address.id != ''" @click="choiceAddress">
			<h3 class="phone">{{address.name}} {{address.phone}}</h3>
			<view class="text">{{address.address}}</view>
			<text class="icon icon-z-right"></text>
		</view>

		<view class="addre" v-else @click="choiceAddress">
			<view class="text">选择地址</view>
			<text class="icon icon-z-right"></text>
		</view>

		<view class="a-tea">
			<view class="top">
				<view class="a-img">
					<img :src="$utils.imageUrl(goodsinfo.head_img)" style="width:100%;height:100%;" mode="widthFix">
				</view>
				<view class="tree">
					<view>
						<text class="title">{{goodsinfo.goodsname}}</text>
						<text class="price">￥1080</text>
					</view>
					<view>
						<text class="title1">规格：礼盒装</text>
						<text class="num">×1</text>
					</view>
				</view>
			</view>
			<view class="bottom">
				<view class="z-time-wrap">
					<text class="day">配送</text>
					<text class="date">{{tea.day}}</text>
				</view>
				<view class="liuyan">
					<text class="say">给商家留言</text>
					<textarea class="message" type="text" v-if="showInput" v-model="remark" placeholder="选填，可备注联系人，备用电话，订单说明等" ></textarea>
				</view>
			</view>
		</view>
		<button class="button" @click="sub">立即兑换</button>
		
		
		
		<!-- 绑定成功提示 -->
		<view class="success-pop" v-if="showPop">
			<view class="pop-center clearfix">
				<image @click="close" class="close" src="../../static/z-close.png" mode="widthFix"></image>
				<view class="p">恭喜您，成功兑换礼品！</view>
				<navigator class="n" hover-class="none" url="./change">立即查看</navigator>
			</view>
		</view>
	</view>
</template>

<script>
	import config from '../../common/config.js';
	export default {
		data() {
			return {
				goodsinfo: [],
				address: {},
				good_keynum: "",
				remark: "",
				showInput: true,
				showPop: false
			}
		},
		onLoad: function(e) {
			console.log(e);
			uni.showToast({
				icon: "loading",
				title: "加载中"
			})
			var good_keynum = e.good_keynum;
			var memberid = uni.getStorageSync('id')
			this.memberid = memberid;
			this.good_keynum = good_keynum;
			var action = 'get_goods_detail';
			var data = JSON.stringify({
				keynum: good_keynum,
				memberid: memberid,
			});

			this.$utils.post(action, data).then(res => {
				console.log('获取商品详细信息', res)
				this.goodsinfo = res.rs.goodsinfo;
			})
		},
		onShow: function(e) {
			this.$set(this.address, 'id', uni.getStorageSync('member_area_id'));
			this.$set(this.address, 'name', uni.getStorageSync('member_area_linkman'));
			this.$set(this.address, 'phone', uni.getStorageSync('member_area_linktel'));
			this.$set(this.address, 'address', uni.getStorageSync('member_area_address'));

		},
		methods: {
			close: function(){
				this.showInput = true;
				this.showPop = false;
			},
			
			sub: function(e) {
				//获取需要的数据

				let data = JSON.stringify({
					memberid: this.memberid,
					member_area_id: this.address.id,
					cardid: uni.getStorageSync('dh_cardid'),
					goodsid: this.goodsinfo.id,
					goods_model: this.goodsinfo.goods_model,
					goods_spec: this.goodsinfo['goods_spec'][0]['id'],
					goods_item: this.goodsinfo['goods_spec'][0]['goods_spec_item'][0]['item'],
					delivery_type: '快递',
					remark: this.remark

				});
				console.log(data);

				var action = 'exchange_giftcard';
				this.$utils.post(action, data).then(res => {
					console.log(res);
					if (res.sta == 1) {
						this.showInput = false;
						this.showPop = true;
						// uni.showModal({
						// 	title: '',
						// 	content: '恭喜您，兑换成功！',
						// 	success: function(res1) {
						// 		if (res1.confirm) {
						// 			uni.navigateTo({ //跳转页面
						// 				url: "./change"
						// 			})
						// 		} else if (res1.cancel) {
						// 			console.log('用户点击取消');
						// 		}
						// 	}
						// });
					} else {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						})


					}
				})
			},
			choiceAddress: function() {
				uni.navigateTo({
					url: '../Add/Add?cardbag_number=5',
				});
			},
		}
	}
</script>

<style>
	.address{
		padding-bottom: 110rpx;
	}
	
	.addre {
		padding: 40rpx 100rpx 30rpx 56rpx;
		background-color: #fff;
		margin-top: 20rpx;
		position: relative;
	}

	.addre .icon {
		font-size: 32rpx;
		color: #333;
		position: absolute;
		right: 32rpx;
		top: 50%;
		transform: translateY(-50%);
	}

	.phone {
		font-size: 32rpx;
		color: #333;
		font-weight: bold;
		line-height: 1.8em;
		margin-bottom: 10rpx;
	}

	.text {
		font-size: 26rpx;
		color: #666;
		line-height: 1.8em;
	}

	.a-tea {
		box-sizing: border-box;
		width: 100%;
		height: 360rpx;
		background-color: #fff;
		margin-top: 20rpx;
		position: relative;
	}

	.top {
		box-sizing: border-box;
		width: 100%;
		display: flex;
		padding: 40rpx 38rpx;
	}

	.a-img {
		width: 140rpx;
		height: 140rpx;
		background-color: #F3F3F3;
	}

	.a-tea img {
		height: 100rpx;
	}

	.tree {
		margin-left: 20rpx;
		flex: 1;
		width: 80%;
		padding-top: 10rpx;
	}
	.tree > view{
		display: flex;
	}
	.tree > view:first-child{  margin-bottom: 17rpx;}

	.title {
		width: 80%;
		flex: 1;
		color: #333;
		font-size: 30rpx;
		margin-right: 30rpx;
		line-height: 1.8em;
		font-weight: 500;
	}

	.title1 {
		width: 80%;
		flex: 1;
		color: #999;
		font-size: 24rpx;
		margin-right: 30rpx;
	}

	.price {
		font-size: 30rpx;
	}

	.num {
		
		color: #999;
		font-size: 24rpx;
	}

	.bottom {
		padding: 26rpx 38rpx;
		background-color: #fff;
	}
	.z-time-wrap{
		margin-bottom: 60rpx;
	}
	.day {
		font-weight: bold;
		color: #333;
		font-weight: bold;
		font-size: 30rpx;
	}

	.date {
		color: #ccc;
		font-weight: normal;
		float: right;
		font-size: 30rpx;
	}
	.liuyan{
		 display: flex;
	}
	.say {
		font-size: 24rpx;
		color: #666;
	}

	.message {
		font-size: 24rpx;
		color: #999;
		width: 80%;
		flex: 1;
		text-align: right;
		display: inline-block;
		align-items: baseline;
		margin-left: 40rpx;
	}

	.button {
		position: fixed;
		bottom: 0;
		background-color: #EC1815;
		color: #fff;
		width: 100%;
		height: 105rpx;
		line-height: 105rpx;
		border-radius: 0!important;
		
	}
</style>
